body {
  nav {
    position: sticky;
    top: 0%;
    border-top: red solid 5px;
    background: rgba(255, 255, 255, 0.89);
    height: 95px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 1rem;
    align-items: center;

    a {
      text-decoration: none;
      color: black;
      text-transform: uppercase;
      font-size: 14px;
      margin: 0 20px;
      transition: .2s all ease-in-out;

      &:hover {
        color: red;
      }
    }

    @include laptop {
      a {
        margin: 0;
      }
    }
    @include tablet {
      grid-template-columns: repeat(5, 1fr);
      justify-items: center;
      .hide {
        display: none;
      }
    }
  }
}

header {
  height: 100vh;
  background: url('../../img/master-slides-02.jpg') center/cover;
  
  .header {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 40vh auto auto;
    justify-items: center;
    .welcome {
      align-self: end;
      font-family: 'Courgette', cursive;
      color: white;
      font-size: 60px;
    }
    .patoplace {
      font-family: 'Poppins', sans-serif;
      color: white;
      font-size: 90px;
      margin-top: 0;
      letter-spacing: 10px;
      word-spacing: 10px;
      text-align: center;
    }

    .btn {
      text-decoration: none;
      color: red;
      background: white;
      padding: 15px 25px;
      border-radius: 10px;
      text-transform: uppercase;
      transition: 0.2s all ease-in-out;
      font-size: 12px;

      &:hover {
        color: white;
        background: red;
      }
    }
  }
}

.italianresto {
  // height: 40vh;
  text-align: center;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 40vh;
  align-items: center;
  margin-bottom: 70px;

  @include mobile {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;

    .left {
      padding-top: 70px;
    }
  }

  .left {
    width: 80%;
    margin: 0 auto;
  }

  .right {
    width: 390px;
    height: 390px;
    display: block;
    margin: 0 auto;

    .image {
      width: 100%;
      height: 100%;
      margin: 40px auto;
      background: url('../../img/our-menu-02.jpg') center/cover;
      border-radius: 10px;
      background-size: 100%;
      transition: .5s all ease-in-out;

      &:hover {
        background-size: 110%;
      }

      @include tablet {
        width: 300px;
        height: 300px;
      }
    }
  }
}

