.ourmenu {
  background-color: $menubg;
  .top {
    text-align: center;
    padding-top: 60px;
  }

  .bot {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 230px);
    gap: 2rem;
    padding-bottom: 120px;
    grid-template-areas: 
    "ln dn ."
    "ln dn ."
    "hp hp ."
    ;

    @include mobile {
      grid-template-columns: 1fr;
      grid-template-rows: repeat(8, 130px);
      grid-template-areas: 
      "ln"
      "ln"
      "dn"
      "dn"
      "."
      "."
      "."
      "hp"
      ;
    }
  }

  div {
    background-size: 105%;
    transition: .2s all ease-in-out;

    &:hover {
      background-size: 115%;
    }
  }

  .ln {
    grid-area: ln;
    background: url('../../img/our-menu-01.jpg') center/cover;
    border-radius: 10px;
    background-size: 105%;
    transition: .5s all ease-in-out;
  }

  .dn {
    grid-area: dn;
    background: url('../../img/our-menu-05.jpg') center/cover;
    border-radius: 10px;
    background-size: 105%;
    transition: .5s all ease-in-out;
  }

  .dr {
    background: url('../../img/our-menu-08.jpg') center/cover;
    border-radius: 10px;
    background-size: 105%;
    transition: .5s all ease-in-out;
  }

  .strt {
    background: url('../../img/our-menu-10.jpg') center/cover;
    border-radius: 10px;
    background-size: 105%;
    transition: .5s all ease-in-out;
  }

  .hp {
    grid-area: hp;
    background: url('../../img/our-menu-13.jpg') center/cover;
    border-radius: 10px;
    background-size: 105%;
    transition: .5s all ease-in-out;
  }

  .ds {
    background: url('../../img/our-menu-16.jpg') center/cover;
    border-radius: 10px;
    background-size: 105%;
    transition: .5s all ease-in-out;
  }
}

.events {
  background: url('../../img/bg-event-03.jpg') center/cover;
  .top {
    text-align: center;
    padding-top: 60px;
    .welc {
      color: white;
    }
  }

  .eventcard {
    // width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 35px 1fr 1fr;
    grid-template-rows: 390px;
    padding-bottom: 180px;

    

    .redband {
      background: red;
      text-align: center;
      writing-mode: vertical-lr;
      color: white;
      font-size: 12px;
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;

      span {
        display: inline-block;
        margin-left: 10px;
      }
    }

    .image {
      background: url('../../img/event-06.jpg') center/cover;
    }

    .content {
      background: white;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      text-align: center;

      h2 {
        text-transform: uppercase;
        margin-top: 55px;
      }

      p {
        width: 90%;
        margin: 35px auto;
      }

      .date {
        width: 90%;
        margin: 40px auto;
        display: grid;
        grid-template-columns: repeat(4, 1fr);

        .number {
          text-transform: uppercase;
          font-size: 50px;
          color: red;
          font-weight: 900;
          margin-bottom: 5px;
        }

        .name {
          text-transform: uppercase;
          font-size: 12px;
          font-weight: 700;
          color: grey;
        }
      }
    }
    @include mobile {
      grid-template-columns: 1fr;
      grid-template-rows: 70px 1fr 1fr;

      .redband {
        writing-mode: horizontal-tb;
        border-bottom-left-radius: 0;
        border-top-right-radius: 10px;
        margin-top: 10px;
        margin-left: 0;
      }

      .content {
        border-top-right-radius: 0px;
        border-bottom-left-radius: 10px;
      }
    }
  }
}

.book {
  text-align: center;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 40vh;
  align-items: center;
  margin-bottom: 70px;

  @include mobile {
    grid-template-columns: 1fr;
  }

  .left {
    width: 80%;
    margin: 0 auto;

    .form {
      display: grid;
      grid-template-columns: 1fr 1fr;
      text-align: left;

      & * {
        margin: 5px;
      }

      input {
        height: 2.5rem;
        width: 95%;
        border-radius: 5px;
        border: 2px rgba(128, 128, 128, 0.472) solid;
        
        &::placeholder {
          font-size: 14px;
        }

        &:focus {
          border: rgba(255, 0, 0, 0.938);
        }
      }

      select {
        height: 2.9rem;
        width: 95%;
        border-radius: 5px;
        border: 2px rgba(128, 128, 128, 0.472) solid;

        &:focus {
          border-color: red;
        }
      }
    }
  }

  .right {
    width: 390px;
    height: 390px;
    display: block;
    margin: 0 auto;

    .image {
      width: 100%;
      height: 100%;
      margin: 40px auto;
      background: url('../../img/booking-01.jpg') center/cover;
      border-radius: 10px;
      background-size: 100%;
      transition: .5s all ease-in-out;

      &:hover {
        background-size: 110%;
      }
    }
  }
}

