.signup {
  height: 15vh;
  background: $menubg;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(3, auto);
  justify-content: center;
  align-items: center;
  align-content: center;

  div {
    margin: 1rem;
  }

  .specials {
    font-family: 'Poppins', sans-serif;
    font-size: 22px;
    letter-spacing: 3px;
  }

  .btn-dark {
    margin: 0;
  }

  input {
    height: 2.5rem;
    width: 95%;
    border-radius: 5px;
    border: 2px rgba(128, 128, 128, 0.472) solid;
    
    &::placeholder {
      font-size: 14px;
    }

    &:focus {
      border: rgba(255, 0, 0, 0.938);
    }
  }
}

footer {
  .part1 {
    background: $foot1;
    color: white;
    padding-top: 50px;
    padding-bottom: 80px;

    .part1wrapped {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 2rem;

      @include mobile {
        grid-template-columns: 1fr;
        text-align: center;
      }

      h2 {
        text-transform: uppercase;
        font-size: 18px;
        font-family: 'Poppins', sans-serif;
        margin: 30px 0;
      }

      span {
        font-size: 13px;
        color: gray;
      }

      .cont, .opentime, .tweet {
        display: grid;
        grid-template-columns: 1fr;
        gap: 1rem;

        p {
          font-size: 13px;
          color: gray;
        }
      }

      .tweetl {
        text-decoration: none;
        color: red;
      }
    }

    .gall {
      display: grid;
      grid-template-columns: repeat(4, 80px);
      grid-template-rows: repeat(3, 80px);
      gap: 1rem;
      justify-content: center;
      .gal {
        background: url('../../img/photo-gallery-01.jpg') center/cover;
      }
    }
  }

  .finalfooter {
    background: $foot2;
    color: grey;
    padding-top: 20px;
    padding-bottom: 20px;

    .part2wrapped {
      display: grid;
      grid-template-columns: 30% 1fr;
      gap: 1rem;
      justify-items: end;
    }
  }
}

.burgerbtn {
  width: 30px;
  height: 30px;
  div {
    border-top: 2px black solid;
    margin-top: 2px;
    height: 10px;
    width: 25px;
    transition: .2s all ease-in-out;
  }

  &:hover {
    cursor: pointer;

    div {
      border-top: 2px red solid;
    }
  }
}